import { authFetch } from "./auth";
import { baseUrl } from "./const";
import { urlBase64 } from "./util";
export const fetchLicenses = async (licenseIssuerID) => {
    const res = await authFetch(`${baseUrl}/api/license-issuers/${licenseIssuerID}/licenses`);
    if (!res.ok) {
        return;
    }
    const json = await res.json();
    return json.map((v) => parseLicense(v));
};
export const fetchLicense = async (licenseIssuerID, licenseID) => {
    const _licenseID = urlBase64(licenseID);
    const res = await authFetch(`${baseUrl}/api/license-issuers/${licenseIssuerID}/licenses/${_licenseID}`);
    if (!res.ok) {
        return;
    }
    const json = await res.json();
    return parseLicense(json);
};
export const createLicense = async (issuerID, fields) => {
    const res = await authFetch(`${baseUrl}/api/license-issuers/${issuerID}/licenses`, {
        method: "POST",
        headers: {
            "Content-Type": "application/json",
        },
        body: JSON.stringify(fields),
    });
    if (!res.ok) {
        return {
            license: null,
            status: res.status,
        };
    }
    const json = await res.json();
    return {
        license: parseLicense(json),
        status: res.status,
    };
};
export const deleteLicense = async (licenseIssuerID, licenseID) => {
    const _licenseID = urlBase64(licenseID);
    const res = await authFetch(`${baseUrl}/api/license-issuers/${licenseIssuerID}/licenses/${_licenseID}`, {
        method: "DELETE",
    });
    return res.ok;
};
export const updateLicense = async (issuerID, licenseID, fields) => {
    const _licenseID = urlBase64(licenseID);
    const res = await authFetch(`${baseUrl}/api/license-issuers/${issuerID}/licenses/${_licenseID}`, {
        method: "PATCH",
        headers: {
            "Content-Type": "application/json",
        },
        body: JSON.stringify(fields),
    });
    if (!res.ok) {
        return {
            license: null,
            status: res.status,
        };
    }
    const json = await res.json();
    return {
        license: parseLicense(json),
        status: res.status,
    };
};
const parseLicense = (json) => {
    return {
        ID: json.id,
        Key: json.key,
        Active: json.active,
        Name: json.name,
        Tags: json.tags,
        EndUserEmail: json.endUserEmail,
        Note: json.note,
        Data: json.data,
        MaxSessions: json.maxSessions,
        ValidUntil: json.validUntil ? new Date(json.validUntil) : null,
        LastUsed: json.lastUsed ? new Date(json.lastUsed) : null,
        Created: new Date(json.created),
        Updated: new Date(json.updated),
        ProductID: json.productID,
    };
};
export const parseTags = (str) => {
    const tags = str.split(";").map((v) => v.trim());
    if (tags.length > 0 && tags[tags.length - 1] === "") {
        return tags.slice(0, -1);
    }
    return tags;
};
export const formatTags = (tags) => {
    return tags.join("; ");
};
export const parseLicenseFields = (data) => {
    try {
        const str = atob(data);
        const kv = Object.entries(JSON.parse(str));
        const ls = kv.map((v) => ({
            _id: Math.random(),
            field: v[0],
            value: v[1],
        }));
        return ls;
    }
    catch (_a) {
        return null;
    }
};
export const formatLicenseFields = (fields) => {
    let obj = {};
    (fields || []).forEach((v) => {
        obj[v.field] = v.value;
    });
    const str = JSON.stringify(obj);
    return btoa(str);
};
