import { navigate } from "svelte-navigator";
import { derived, get } from "svelte/store";
import { baseUrl } from "./const";
import { localStorageWritable } from "./localStorage";
const authData = localStorageWritable("authData", {
    licenseIssuerID: -1,
    token: "",
});
export const loggedIn = derived(authData, (data) => data.token !== "");
export const issuerID = derived(authData, (data) => data.licenseIssuerID);
export const isPrivileged = derived(authData, (data) => data.licenseIssuerID === 0);
export const login = async (username, password) => {
    const res = await fetch(`${baseUrl}/api/login`, {
        method: "POST",
        headers: {
            "Content-Type": "application/json",
        },
        body: JSON.stringify({
            username,
            password,
        }),
    });
    if (!res.ok) {
        return false;
    }
    const json = await res.json();
    authData.set(json);
    return true;
};
export const logout = () => {
    authData.set({
        licenseIssuerID: -1,
        token: "",
    });
};
export const changePasswd = async (licenseIssuerID, fields) => {
    const res = await authFetch(`${baseUrl}/api/change-password/${licenseIssuerID}`, {
        method: "PATCH",
        headers: {
            "Content-Type": "application/json",
        },
        body: JSON.stringify(fields),
    });
    return { ok: res.ok, status: res.status };
};
export const authFetch = async (input, init) => {
    const token = get(authData).token;
    const res = await fetch(input, Object.assign(Object.assign({}, init), { headers: Object.assign({ Authorization: `Bearer ${token}` }, init === null || init === void 0 ? void 0 : init.headers) }));
    switch (res.status) {
        case 401: {
            logout();
            navigate("/login");
            break;
        }
        case 403: {
            navigate("/");
            break;
        }
    }
    return res;
};
