import { authFetch } from "./auth";
import { baseUrl } from "./const";
export const fetchLicenseIssuer = async (licenseIssuerID) => {
    const res = await authFetch(`${baseUrl}/api/license-issuers/${licenseIssuerID}`);
    if (!res.ok) {
        return {};
    }
    const json = await res.json();
    return parseLicenseIssuer(json);
};
export const fetchLicenseIssuers = async () => {
    const res = await authFetch(`${baseUrl}/api/license-issuers`);
    if (!res.ok) {
        return [];
    }
    const json = await res.json();
    return json.map((v) => parseLicenseIssuer(v));
};
export const editLicenseIssuer = async (id, fields) => {
    const res = await authFetch(`${baseUrl}/api/license-issuers/${id}`, {
        method: "PATCH",
        headers: {
            "Content-Type": "application/json",
        },
        body: JSON.stringify(fields),
    });
    if (!res.ok) {
        return {
            licenseIssuer: null,
            status: res.status,
        };
    }
    const json = await res.json();
    return {
        licenseIssuer: parseLicenseIssuer(json),
        status: res.status,
    };
};
export const deleteLicenseIssuer = async (id) => {
    const res = await authFetch(`${baseUrl}/api/license-issuers/${id}`, {
        method: "DELETE",
    });
    return res.ok;
};
export const createLicenseIssuer = async (fields) => {
    const res = await authFetch(`${baseUrl}/api/license-issuers`, {
        method: "POST",
        headers: {
            "Content-Type": "application/json",
        },
        body: JSON.stringify(fields),
    });
    if (!res.ok) {
        return {
            licenseIssuer: null,
            status: res.status,
        };
    }
    const json = await res.json();
    return {
        licenseIssuer: parseLicenseIssuer(json),
        status: res.status,
    };
};
const parseLicenseIssuer = (json) => {
    return {
        ID: json.id,
        Active: json.active,
        Username: json.username,
        Email: json.email,
        PhoneNumber: json.phoneNumber,
        MaxLicenses: json.maxLicenses,
        Created: new Date(json.created),
        Updated: new Date(json.updated),
    };
};
