import { authFetch } from "./auth";
import { baseUrl } from "./const";
export const fetchProducts = async (licenseIssuerID) => {
    const res = await authFetch(`${baseUrl}/api/license-issuers/${licenseIssuerID}/products`);
    if (!res.ok) {
        return;
    }
    const json = await res.json();
    return json.map((v) => parseProduct(v));
};
export const fetchProduct = async (licenseIssuerID, productID) => {
    const res = await authFetch(`${baseUrl}/api/license-issuers/${licenseIssuerID}/products/${productID}`);
    if (!res.ok) {
        return;
    }
    const json = await res.json();
    return parseProduct(json);
};
export const createProduct = async (licenseIssuerID, fields) => {
    const res = await authFetch(`${baseUrl}/api/license-issuers/${licenseIssuerID}/products`, {
        method: "POST",
        headers: {
            "Content-Type": "application/json",
        },
        body: JSON.stringify(fields),
    });
    if (!res.ok) {
        return {
            product: null,
            status: res.status,
        };
    }
    const json = await res.json();
    return {
        product: parseProduct(json),
        status: res.status,
    };
};
export const updateProduct = async (licenseIssuerID, productID, fields) => {
    const res = await authFetch(`${baseUrl}/api/license-issuers/${licenseIssuerID}/products/${productID}`, {
        method: "PATCH",
        headers: {
            "Content-Type": "application/json",
        },
        body: JSON.stringify(fields),
    });
    if (!res.ok) {
        return {
            product: null,
            status: res.status,
        };
    }
    const json = await res.json();
    return {
        product: parseProduct(json),
        status: res.status,
    };
};
export const deleteProduct = async (licenseIssuerID, productID) => {
    const res = await authFetch(`${baseUrl}/api/license-issuers/${licenseIssuerID}/products/${productID}`, {
        method: "DELETE",
    });
    return res.ok;
};
const parseProduct = (json) => {
    return {
        ID: json.id,
        Active: json.active,
        Name: json.name,
        ContactEmail: json.contactEmail,
        Data: json.data,
        Created: new Date(json.created),
        Updated: new Date(json.updated),
    };
};
